.hidden-menu {
  display: block;
  list-style: none;
  padding: 0 10px;
  overflow-y: auto;
  margin: 0;
  box-sizing: border-box;
  width: 50%;
  background-color: #3A1C34;
  height: 100vh;
  position: fixed;
  top: 0;
  right: -50%;
  transition: right 0.2s;
  z-index: 2;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden; }
  @media (min-width: 768px), (orientation: landscape) {
    .hidden-menu {
      width: 30%; } }

.hidden-menu-ticker {
  display: none; }

.btn-menu {
  color: #FFF;
  background-color: #3A1C34;
  padding: 5px;
  position: fixed;
  top: 6vmin;
  right: 6vmin;
  cursor: pointer;
  transition: right 0.23s;
  z-index: 3;
  width: 25px;
  border-radius: 5px;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden; }
  .btn-menu span {
    display: block;
    height: 5px;
    background-color: #FFF;
    margin: 5px 0 0;
    transition: all 0.1s linear 0.23s;
    position: relative; }
    .btn-menu span.first {
      margin-top: 0; }
  @media (min-width: 768px) {
    .btn-menu {
      top: calc((15.5vh / 3) + 4.5vh);
      right: 5.3vh; } }
  @media (min-width: 1280px) {
    .btn-menu {
      display: none; } }
  @media only screen and (min-width: 1024px) and (max-width: 1366px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 1.5) {
    .btn-menu {
      display: block; } }

.hidden-menu-ticker:checked ~ .btn-menu {
  top: 0;
  right: 50%;
  border-radius: 5px 0 0 5px;
  background-color: #3A1C34; }
  @media (min-width: 768px), (orientation: landscape) {
    .hidden-menu-ticker:checked ~ .btn-menu {
      right: 30%; } }

.hidden-menu-ticker:checked ~ .hidden-menu {
  right: 0; }

.hidden-menu-ticker:checked ~ .btn-menu span.first {
  -webkit-transform: rotate(45deg);
  top: 10px; }

.hidden-menu-ticker:checked ~ .btn-menu span.second {
  opacity: 0; }

.hidden-menu-ticker:checked ~ .btn-menu span.third {
  -webkit-transform: rotate(-45deg);
  top: -10px; }

.hidden-menu-link {
  font-family: "Playfair Display SC", serif;
  font-weight: 700;
  color: #FFF;
  text-decoration: none;
  text-transform: uppercase; }
  .hidden-menu-link:hover, .hidden-menu-link:focus, .hidden-menu-link:active {
    color: #CB74C0; }

.hidden-menu-link-holder {
  margin-top: 3vh; }
  @media (min-width: 768px) {
    .hidden-menu-link-holder:first-of-type {
      margin-top: 7.5vh; } }

